@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body{margin:0;padding:0;height:100vh;font-family:'Varela Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

#nav-container{display:flex;flex-direction:row;justify-content:space-between;background-color:#333}#nav-container #logo-left{height:140px;display:flex;align-items:flex-end;width:20vw;margin-left:75px;margin-bottom:10px}#nav-container #logo-left a{margin:0;padding:0;text-transform:none;text-decoration:none}#nav-container #logo-left a img{margin:0;padding:0;width:50px;height:auto;padding:10px;border:2px solid #f8f8f8;border-radius:50%;margin-bottom:10px}#nav-container #rightside-container{height:140px;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end;margin-bottom:10px}#nav-container #rightside-container .profile-container{display:flex;flex-direction:row;justify-content:center;align-items:flex-end;margin-right:85px;margin-bottom:20px}#nav-container #rightside-container .profile-container h4{margin:0;padding:0;margin-right:25px;margin-bottom:6px;letter-spacing:2px;color:#f8f8f8}#nav-container #rightside-container .profile-container .reg-button{margin:0 0 0 25px;padding:0;padding:8px 30px 5px 30px;border:1px solid transparent;border-radius:10px;background:#f8f8f8;color:#333;font-size:1.15rem;cursor:pointer;transition-duration:0.25s;text-decoration:none}#nav-container #rightside-container .profile-container .login-button{margin:0;padding:0;padding:8px 30px 5px 30px;border:1px solid transparent;border-radius:10px;background:#f8f8f8;color:#333;font-size:1.15rem;cursor:pointer;transition-duration:0.25s;text-decoration:none}#nav-container #rightside-container .profile-container .login-button:hover,#nav-container #rightside-container .profile-container .login-button:focus{background:transparent;color:#f8f8f8;border:1px solid #f8f8f8}#nav-container #rightside-container .profile-container .reg-button:hover,#nav-container #rightside-container .profile-container .reg-button:focus{background:transparent;color:#f8f8f8;border:1px solid #f8f8f8}#nav-container #rightside-container .profile-container .is-active{color:#f8f8f8;background:transparent;border:1px solid #f8f8f8}#nav-container #rightside-container .navlink-container{margin:0;padding:0;margin-bottom:10px;margin-right:75px;width:80vw;display:flex;flex-direction:row;justify-content:flex-end}#nav-container #rightside-container .navlink-container a{position:relative;font-size:1.15rem;font-weight:500;color:#f8f8f8;text-decoration:none;cursor:pointer;z-index:0;padding:5px 20px;border-radius:30px;margin-left:52.5px}#nav-container #rightside-container .navlink-container a:before,#nav-container #rightside-container .navlink-container a:after{position:absolute;opacity:0;font-size:35px;top:-6px;transition:all 0.3s}#nav-container #rightside-container .navlink-container a:before{content:'(';left:0px;-webkit-transform:translateX(10px);transform:translateX(10px)}#nav-container #rightside-container .navlink-container a:after{content:')';right:0px;-webkit-transform:translateX(-10px);transform:translateX(-10px)}#nav-container #rightside-container .navlink-container .is-active:before{content:'(';opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container #rightside-container .navlink-container .is-active:after{content:')';opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container #rightside-container .navlink-container a:hover:before{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container #rightside-container .navlink-container a:hover:after{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}@media screen and (max-width: 910px){#nav-container{display:grid}#nav-container #logo-left{margin:0;padding:0;height:75px;width:100vw;display:grid;place-items:center;background-color:black}#nav-container #logo-left a img{margin:0;padding:0;width:25px;height:auto;padding:10px;border:2px solid black;border-radius:50%}#nav-container #rightside-container{display:none}#nav-container{height:100px}}

.login-container{width:100vw;height:auto;margin-top:100px;display:grid;place-items:center}.login-container form{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.login-container form label{font-size:2.5rem}.login-container form input{margin-top:50px;padding-left:10px;border:none;border-bottom:1px solid grey;background-color:transparent;font-size:1.25rem}.login-container form input:focus{outline:none}.login-container form button{margin-top:50px;padding:10px 30px;border:1px solid #333;border-radius:10px;color:#f8f8f8;background-color:#333;font-size:1.5rem;cursor:pointer;transition-duration:0.25s}.login-container form button:hover{color:#333;background-color:#f8f8f8}

.login-container{width:100vw;height:auto;margin-top:100px;display:grid;place-items:center}.login-container form{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.login-container form label{font-size:2.5rem}.login-container form input{margin-top:50px;padding-left:10px;border:none;border-bottom:1px solid grey;background-color:transparent;font-size:1.25rem}.login-container form input:focus{outline:none}.login-container form button{margin-top:50px;padding:10px 30px;border:1px solid #333;border-radius:10px;color:#f8f8f8;background-color:#333;font-size:1.5rem;cursor:pointer;transition-duration:0.25s}.login-container form button:hover{color:#333;background-color:#f8f8f8}

#landing-page{width:100vw}#landing-page .particle-background{display:none;position:absolute;width:100vw;height:125vh}@media screen and (max-width: 910px){.particle-background{height:100vh}}

#infocarousel-container{margin-top:100px}#infocarousel-container #ic-description-container{width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container #ic-description-container h1{margin:0;padding:0;margin-left:1vw;margin-top:50px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#infocarousel-container .info-carousel{margin-bottom:25px;width:80vw;height:40vw;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#infocarousel-container .info-carousel img{height:100vh;width:auto}#infocarousel-container .description{display:none}.swiper-button-prev{color:white;left:0;padding-left:25px}.swiper-button-next{color:white;right:0;padding-right:25px}@media screen and (max-width: 910px){#infocarousel-container #ic-description-container{width:90vw}#infocarousel-container #ic-description-container h1{font-size:1.5rem}#infocarousel-container .info-carousel{width:90vw;height:60vw}#infocarousel-container .info-carousel img{width:auto;height:100%}#infocarousel-container .description{display:block;width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container .description h1{font-family:"Montserrat", 'sans-serif';font-size:1.75rem;font-weight:700}#infocarousel-container .description p{font-family:"Montserrat", 'sans-serif';font-size:0.85rem;letter-spacing:1px;margin:0;padding:0;font-weight:300}.swiper-button-next,.swiper-button-prev{display:none}}

#dashboard-title{margin:0;padding:0;margin-left:11vw;margin-top:100px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#dashboard-container{left:0;right:0;margin:auto;width:80vw;height:auto;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-between;align-items:flex-start}#dashboard-container .Gifts{width:42.5vw;height:35vw;display:flex;flex-direction:column;justify-content:space-between}#dashboard-container .Gifts .top-user{width:100%;height:35%;display:grid;place-items:center;background-color:rgba(0,0,0,0.25);border-radius:30px;color:white}#dashboard-container .Gifts .top-user div{width:90%;height:70%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}#dashboard-container .Gifts .top-user div h1{font-size:3rem;padding:10px 20px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center;align-items:center}#dashboard-container .Gifts .top-user div .description p{margin:0;padding:0;width:90%}#dashboard-container .Gifts .top-user div .description div{width:90%;height:17.5px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{width:100%;height:60%;display:grid;place-items:center;background-color:rgba(0,0,0,0.25);border-radius:30px}@media screen and (max-width: 910px){#dashboard-title{font-size:1.5rem;margin-left:6vw}#dashboard-container{width:90vw}#dashboard-container .Gifts{width:100%;height:auto}#dashboard-container .Gifts .top-user{height:35vw;margin-top:30px}#dashboard-container .Gifts .top-user div h1{font-size:2rem;padding:11.5px 17.5px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description p{font-size:0.65rem}#dashboard-container .Gifts .top-user div .description div{width:90%;height:12px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{height:50vw;margin-top:30px}}

#top-donations{display:grid;place-items:center;margin:0;padding:0;width:35vw;height:35vw;background-color:#f8f8f8;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#top-donations .chart-title-container{width:100%;display:grid;place-items:center}#top-donations .chart-title-container h3{margin:15px 0 0 0;padding:0;color:#4d4d4d;font-size:1.15rem;font-family:'Varela Round', sans-serif}#top-donations .chart-button-container{width:100%;height:auto;display:flex;flex-direction:row;justify-content:space-evenly;margin-bottom:10px}#top-donations .chart-button-container .see_chart_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;margin:0;padding:10px 30px;border:1px solid black;border-radius:10px;background:#f8f8f8;cursor:pointer}#top-donations .chart-button-container .see_data_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;color:white;margin:0;padding:10px 30px;border:none;border-radius:10px;background:#333;cursor:pointer}@media screen and (max-width: 910px){#top-donations{width:100%;min-height:400px}#top-donations .chart-title-container h3{font-size:1rem}#top-donations .chart-button-container .see_chart_button{font-size:1rem;border:1px solid black}#top-donations .chart-button-container .see_data_button{font-size:1rem}}@media screen and (max-width: 410px){#top-donations{min-height:375px}#top-donations .chart-title-container h3{font-size:0.75rem}#top-donations .chart-button-container{margin-bottom:7.5px}}


.App{height:auto;background-color:#f8f8f8;overflow:hidden}.App .Content{width:100vw;display:grid;place-items:center;padding-bottom:100px}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

